    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h1 3.3. Resolución de imprevistos durante la interpretación patrimonial

    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-up")
      p La gestión de imprevistos durante la interpretación patrimonial en entornos étnicos requiere de un protocolo técnico estructurado que garantice la seguridad integral de los participantes y la protección del patrimonio cultural y natural.
      .col-md-11
        p Estos imprevistos se clasifican en tres categorías principales:
        .p •	Logísticos (alteraciones climáticas, fallas en equipos),
        .p •	De seguridad (malestares físicos, emergencias médicas), y
        .p •	Socioculturales (preguntas inapropiadas, comportamientos irrespetuosos hacia las tradiciones).

    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-left")
      p Para esta última categoría, es fundamental contar con protocolos co-diseñados y validados con las autoridades tradicionales, estableciendo respuestas estandarizadas que protejan los conocimientos sagrados y mantengan la armonía intercultural.
      .col-md-11
        br
        h3 Clasificación Detallada de Imprevistos y Estrategias de Solución:
        p A continuación, se presentan algunos ejemplos comunes sobre imprevistos en el turismo y estrategias de solución:

    .row.justify-content-center(data-aos="fade-left")
      .col-lg-10
        figure
          img(src='@/assets/curso/temas/tema3/tema3/tema33-tabla16.svg', alt='Texto que describa la imagen')

    br 
    .row.justify-content-center.align-items-center.mb-4(data-aos="flip-left")
      p La implementación de estos protocolos de respuesta requiere capacitación específica en mediación intercultural y primeros auxilios psicológicos. Para los imprevistos socioculturales, se recomienda el uso de frases puente validadas como: “Agradezco su curiosidad; este aspecto forma parte de conocimientos que nuestra comunidad comparte en contextos específicos. Permítame contarle, en cambio, sobre…” Esta aproximación técnica permite mantener la integridad de la interpretación y fortalecer el respeto intercultural.
      p Todos los incidentes, independientemente de su magnitud, deben documentarse en un registro sistemático que permita el análisis posterior y el ajuste de los protocolos, cumpliendo con los estándares de calidad del servicio y los principios de mejora continua establecidos en la Norma Técnica Sectorial NTS-TS 003. Ahora que se han dado a conocer los imprevistos y las estrategias de solución, se presenta su aplicación en el siguiente ejemplo técnico normativo: 

    br
    .row.justify-content-center(data-aos="fade-left")
      .col-lg-11
